<template>
    <div class="main">
        <div class="gdcl-main">
            <div class="title-container">
                <div class="title">工单中心</div>
                <div class="title-underline"></div>
            </div>
            <div class="search-container">
                <div class="select-group">
                    <select class="sender-select" v-model="sender">
                        <option value="">请选择传送人</option>
                        <option v-for="senderItem in senderOptions" :key="senderItem" :value="senderItem">
                            {{ senderItem }}
                        </option>
                    </select>
                </div>
                <div class="select-group">
                    <select class="status-select" v-model="selectedStatus">
                        <option value="">请选择工单状态</option>
                        <option v-for="status in statusOptions" :key="status.value" :value="status.value">
                            {{ status.label }}
                        </option>
                    </select>
                </div>
                <button class="search-button" @click="handleSearch">查询</button>
                <button class="issue-button" @click="handleIssue">下发</button>
            </div>
            <div class="divider"></div>
            <div class="table-container">
                <table class="order-table">
                    <thead>
                        <tr>
                            <th>传送人</th>
                            <th>创建时间</th>
                            <th>工单状态</th>
                            <th>工单类型</th>
                            <th>操作</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="(order, index) in orderList" :key="index" @click="selectRow(order)">
                            <td>{{ order.sender }}</td>
                            <td>{{ order.createTime }}</td>
                            <td>
                                <span :class="['status-badge', getStatusClass(order.status)]">
                                    {{ order.status }}
                                </span>
                            </td>
                            <td>{{ order.type }}</td>
                            <td>
                                <button class="detail-button" @click.stop="showOrderDetail(order)">详情</button>
                                <button class="update-button" @click.stop="updateOrder(order)">更新</button>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
            <div class="divider-bottom"></div>
        </div>
        
        <!-- 工单设置弹窗 -->
        <div class="modal-overlay" v-if="showIssueModal" @click="closeModal">
            <div class="modal-content" @click.stop>
                <div class="modal-header">
                    <h3>工单设置</h3>
                </div>
                <div class="modal-body">
                    <div class="form-group">
                        <label>工单传送给谁：</label>
                        <select v-model="issueForm.receiver">
                            <option value="">请选择接收人</option>
                            <option v-for="senderItem in senderOptions" :key="senderItem" :value="senderItem">
                                {{ senderItem }}
                            </option>
                        </select>
                    </div>
                    <div class="form-group">
                        <label>工单分类：</label>
                        <select v-model="issueForm.category">
                            <option value="">请选择分类</option>
                            <option value="repair">维修</option>
                            <option value="maintenance">保养</option>
                            <option value="inspection">巡检</option>
                            <option value="other">其他</option>
                        </select>
                    </div>
                    <div class="form-group">
                        <label>工单详细内容：</label>
                        <textarea v-model="issueForm.content" placeholder="请输入工单详细内容"></textarea>
                    </div>
                </div>
                <div class="modal-footer">
                    <button class="cancel-button" @click="closeModal">取消</button>
                    <button class="confirm-button" @click="confirmIssue">确定</button>
                </div>
            </div>
        </div>
        
