                    <h3>工单详情</h3>
                </div>
                <div class="modal-body">
                    <div class="detail-grid">
                        <div class="detail-item">
                            <span class="detail-label">工单编号：</span>
                            <span class="detail-value">{{ selectedOrder.id }}</span>
