                                </span>
                            </span>
                        </div>
                        <div class="detail-item">
                            <span class="detail-label">工单类型：</span>
                            <span class="detail-value">{{ selectedOrder.type }}</span>
                        </div>
                        <div class="detail-item content-item">
                            <span class="detail-label">工单内容：</span>
                            <span class="detail-value content-value">{{ selectedOrder.content }}</span>
                        </div>
                    </div>
                </div>
