                '待处理': 'pending',
                '处理中': 'processing',
                '已完成': 'completed',
                '已取消': 'cancelled'
            };
            return statusMap[status] || '';
        },
        updateOrder(order) {
            // 填充更新表单
            this.updateForm = {
                id: order.id,
                sender: order.sender,
                status: order.status,
                type: order.type,
                content: order.content
            };
