    height: 80%;
    background: rgba(32, 65, 109, 0.7);
    position: relative;
    top: 5%;
    left: 10%;
    text-align: left;
}

.title-container {
    position: relative;
    padding: 20px 0 10px 2%;
}

.title {
    color: white;
    font-size: 22px;
    font-weight: bold;
    letter-spacing: 1px;
}

.title-underline {
    width: 50px;
    height: 2px;
    background: linear-gradient(90deg, #4fc3f7, transparent);
    margin-top: 8px;
}

.search-container {
    margin: 20px 5% 10px auto;
    display: flex;
    gap: 12px;
    width: fit-content;
    float: right;
    align-items: center;
}

.input-group, .select-group {
