    border: 1px solid rgba(255, 255, 255, 0.5);
    border-radius: 4px;
    font-size: 14px;
    background: rgba(255, 255, 255, 0.9);
    transition: all 0.3s ease;
    min-width: 120px;
}

.sender-select:focus, .status-select:focus {
    outline: none;
    border-color: #409eff;
    box-shadow: 0 0 8px rgba(64, 158, 255, 0.4);
}

.search-button, .issue-button {
    padding: 7px 16px;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 14px;
    transition: all 0.3s ease;
}

.search-button {
    background-color: #409eff;
    box-shadow: 0 2px 6px rgba(64, 158, 255, 0.3);
}

.search-button:hover {
    background-color: #66b1ff;
    transform: translateY(-2px);
    box-shadow: 0 4px 10px rgba(64, 158, 255, 0.5);
}

.issue-button {
    background-color: #67c23a;
    box-shadow: 0 2px 6px rgba(103, 194, 58, 0.3);
}

.issue-button:hover {
    background-color: #85ce61;
    transform: translateY(-2px);
    box-shadow: 0 4px 10px rgba(103, 194, 58, 0.5);
}

.divider {
    width: 96%;
    margin: 15px auto;
    border: 0;
    height: 1px;
    background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.7), transparent);
    clear: both;
}

.divider-bottom {
    width: 96%;
    margin: 15px auto;
    border: 0;
    height: 1px;
    background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.7), transparent);
    position: absolute;
    bottom: 0;
    left: 2%;
}

.table-container {
    margin: 20px 2% 20px 2%;
    max-height: calc(100% - 200px);
    overflow-y: auto;
    overflow-x: hidden;
}

.order-table {
    width: 100%;
    border-collapse: collapse;
    background: transparent;
    color: white;
    table-layout: fixed;
}

.order-table th {
    background: rgba(0, 0, 0, 0.2);
    font-weight: bold;
    padding: 12px 10px;
    text-align: left;
}

.order-table td {
    padding: 10px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.order-table tbody tr {
    background: transparent;
    cursor: pointer;
    transition: all 0.2s ease;
}

.order-table tbody tr:hover {
    background: rgba(255, 255, 255, 0.1);
}

.order-table tbody tr:nth-child(even) {
    background: rgba(0, 0, 0, 0.1);
}

.order-table tbody tr:nth-child(even):hover {
    background: rgba(255, 255, 255, 0.15);
}

.detail-button, .update-button {
    padding: 5px 12px;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 12px;
    transition: all 0.3s ease;
    box-shadow: 0 2px 6px rgba(64, 158, 255, 0.3);
    margin-right: 5px;
}

.detail-button {
    background-color: #409eff;
}

.detail-button:hover {
    background-color: #66b1ff;
    transform: translateY(-2px);
    box-shadow: 0 4px 10px rgba(64, 158, 255, 0.5);
}

.update-button {
    background-color: #67c23a;
    box-shadow: 0 2px 6px rgba(103, 194, 58, 0.3);
}

.update-button:hover {
    background-color: #85ce61;
    transform: translateY(-2px);
    box-shadow: 0 4px 10px rgba(103, 194, 58, 0.5);
}

/* 状态徽章样式 */
.status-badge {
    padding: 4px 10px;
    border-radius: 12px;
    font-size: 12px;
    font-weight: 500;
}

.status-badge.pending {
